___CSS_LOADER_EXPORT___.push([module.id, `body {
  background-color: #b6ad90;
  background: linear-gradient(to right, #a4ac86, #b6ad90);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100vh;
}

.container {
  background-color: #fffdfa;
  color: #212427;
  border-radius: 30px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px,
    rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  position: relative;
  overflow: hidden;
  width: 768px;
  max-width: 100%;
  min-height: 480px;
  margin-top: 70px;
}

.container p {
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0.3px;
  margin: 20px 0;
}

.container span {
  text-align: center;
  font-size: 16px;
}

.container a {
  color: #212427;
  font-size: 13px;
  text-decoration: none;
  margin: 15px 0 10px;
}

.container button {
  background-color: #7f4f24;
  color: #fffdfa;
  font-size: 14px;
  padding: 10px 45px;
  border: 2px solid transparent;
  border-radius: 8px;
  font-weight: 600;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  margin-top: 10px;
  cursor: pointer;
  transition: transform 0.1s ease-in-out;
}

.container button.hidden {
  background-color: transparent;
  border-color: #fffdfa;
}

.container button:hover {
  transform: scale(1.03);
  filter: brightness(95%);
}

.container form {
  background-color: #fffdfa;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 40px;
  height: 100%;
}

.container input {
  background-color: #eee;
  border: 2px solid transparent;
  margin: 8px 0;
  padding: 10px 15px;
  font-size: 16px;
  border-radius: 8px;
  width: 100%;
  outline: none;
}
.container input:focus {
  border: 2px solid #7f4f24;
}

.container input:hover {
  background-color: #e1e1e1;
}

.form-container {
  position: absolute;
  top: 0;
  height: 100%;
  transition: all 0.6s ease-in-out;
}

.sign-in {
  left: 0;
  width: 50%;
  z-index: 2;
}

.container.active .sign-in {
  transform: translateX(100%);
}

.sign-up {
  left: 0;
  width: 50%;
  opacity: 0;
  z-index: 1;
}

.container.active .sign-up {
  transform: translateX(100%);
  opacity: 1;
  z-index: 5;
  animation: move 0.6s;
}

@keyframes move {
  0%,
  49.99% {
    opacity: 0;
    z-index: 1;
  }
  50%,
  100% {
    opacity: 1;
    z-index: 5;
  }
}

.toggle-container {
  position: absolute;
  top: 0;
  left: 50%;
  width: 50%;
  height: 100%;
  overflow: hidden;
  transition: all 0.6s ease-in-out;
  border-radius: 250px 0 0 250px;
  z-index: 1000;
}

.container.active .toggle-container {
  transform: translateX(-100%);
  border-radius: 0 250px 250px 0;
}

.toggle {
  background-color: #582f0e;
  height: 100%;
  background: linear-gradient(to right, #936639, #582f0e);
  color: #fffdfa;
  position: relative;
  left: -100%;
  height: 100%;
  width: 200%;
  transform: translateX(0);
  transition: all 0.6s ease-in-out;
}

.container.active .toggle {
  transform: translateX(50%);
}

.toggle-panel {
  position: absolute;
  width: 50%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 30px;
  text-align: center;
  top: 0;
  transform: translateX(0);
  transition: all 0.6s ease-in-out;
}

.toggle-left {
  transform: translateY(-200%);
}

.container.active .toggle-left {
  transform: translateY(0);
}

.toggle-right {
  right: 0;
  transform: translateY(0);
}

.container.active .toggle-right {
  transform: translateY(200%);
}
