___CSS_LOADER_EXPORT___.push([module.id, `.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 80px;
  padding: 1.3rem 10%;
  background: #582f0e;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 100;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px,
    rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}

.logo {
  text-decoration: none;
  color: #fffdfa;
  font-weight: 700;
  position: relative;
}

.link {
  font-size: 18px;
  color: #fffdfa;
  text-decoration: none;
  font-weight: 500;
  margin-left: 2.5rem;
  position: relative;
}

.login-name{
  font-size: 18px;
  color: #fffdfa;
  text-decoration: none;
  font-weight: 500;
}

.logout {
  margin-left: 0;
}

.link::after {
  position: absolute;
  content: '';
  width: 100%;
  height: 3px;
  top: 100%;
  left: 0;
  transition: transform 0.3s;
  transform: scaleX(0);
  transform-origin: right;
  background: #fffdfa;
}

.link:hover:after {
  transform: scaleX(1);
  transform-origin: left;
}
