___CSS_LOADER_EXPORT___.push([module.id, `.table {
  background: #eee;
  text-align: left;
  width: 768px;
  font-size: 1.15rem;
  border-radius: 8px;
  color: #212427;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px,
    rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}

.table td,
.table th {
  padding-bottom: 2%;
  padding-top: 2%;
  padding-left: 2%;
  border-radius: 6px;
}

.table tr {
  background-color: #fffdfa;
}

thead th {
  color: #582f0e;
}

.title {
  padding: 1rem;
  background: #7f4f24;
  color: #fffdfa;
  text-align: center;
  border-radius: 8px;
  margin-bottom: 15px;
  width: 768px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px,
    rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}

.title h2 {
  font-size: 1.15rem;
}

.title h1 {
  font-size: 2rem;
}

.table tbody tr:hover {
  background: #7f4f24;
  color: #fffdfa;
}
